.ChartContainer {
  padding: 1rem 1rem 1rem 0;
  box-sizing: border-box;
}
.Chart {
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  & > h3 {
      margin: 0;
      padding:1rem 1rem 0.5rem 1rem;
  }
}
